*,
::after,
::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
html {
  line-height: 1.15;
  box-sizing: border-box;
}
main {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0;
}
p + p {
  margin-top: 1rem;
}
a {
  background-color: transparent;
}
abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
button,
input,
optgroup,
select,
textarea {
  line-height: inherit;
  border: 1px solid currentColor;
}
button {
  overflow: visible;
  text-transform: none;
}
[type='button'],
[type='reset'],
[type='submit'],
button {
  -webkit-appearance: button;
  padding: 1px 6px;
}
input {
  overflow: visible;
}
input,
textarea {
  padding: 1px;
}
fieldset {
  border: 1px solid currentColor;
  margin: 0 2px;
}
legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
select {
  text-transform: none;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
[type='search'] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type='color'] {
  background: inherit;
}
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.5;
}
::-webkit-file-upload-button,
::-webkit-search-decoration {
  -webkit-appearance: button;
  font: inherit;
}
::-moz-focus-inner {
  border: 0;
}
:-moz-focusring {
  outline: 1px dotted ButtonText;
}
:-moz-ui-invalid {
  box-shadow: none;
}
hr {
  box-sizing: content-box;
  height: 0;
  color: inherit;
  overflow: visible;
}
dl,
ol,
ul {
  margin: 1em 0;
}
dl dl,
dl ol,
dl ul,
ol dl,
ol ol,
ol ul,
ul dl,
ul ol,
ul ul {
  margin: 0;
}
b,
strong {
  font-weight: bolder;
}
audio,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
table {
  text-indent: 0;
  border-color: inherit;
}
details {
  display: block;
}
dialog {
  background-color: inherit;
  border: solid;
  color: inherit;
  display: block;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: fit-content;
}
dialog:not([open]) {
  display: none;
}
summary {
  display: list-item;
}
canvas {
  display: inline-block;
}
template {
  display: none;
}
[hidden] {
  display: none;
}
