.main {
  display: flex;
  padding: 30px 0;
  min-height: calc(100vh - 70px - 70px);
  &Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
  }
}

.errorMessage {
  display: flex;
  align-items: center;
  flex-grow: 1;
  font-size: 20px;
  font-weight: 600;
}
